@import 'src/_variables.scss';

.pagination {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
  width: 100%;
  left: 0;
  bottom: 0;
  right: 0;
  text-align: center;
  text-decoration: none;
  margin: 1rem;
  padding-inline-start: 0;

  @media (max-width: 900px) {
    justify-content: center;
    margin: auto;
    width: 60%;
  }

  &-item {
    list-style: none;
    margin: 0.5rem;
    width: 30px;
    border-radius: 15px;
    border: 1px solid $main-color-alt;

    &:hover {
      background-color: $main-color-alt;
      color: white;
    }

    &.active {
      background-color: $main-color-alt;
      color: white;
    }
  }

  &-link {
    cursor: pointer;
    height: 100%;
    width: 100%;
    display: block;
  }
}
