@import 'src/_variables';

.filters {
  display: flex;
  flex-direction: column;
  
  @media (min-width: 900px) {
    flex-direction: row;
    justify-content: space-evenly;
  }

  &-input {
    width: 100%;
    padding: 1rem;
    background-color: $main-color;
    color: white;
    text-transform: uppercase;
    font-size: 1rem;

    @media (min-width: 900px) {
      width: 200px;
    }
  }

  .button-filters {
    margin: 0;
  }
}