@import 'src/_variables';

.events {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  @media (min-width: 900px) {
    width: 90%;
    margin: auto;
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.event {
  margin: 1rem 0;
  padding-bottom: 1rem;
  background-color: rgba($third-color, 0.8);
  border-top: 5px solid $main-color-alt;
  box-shadow: 2px 2px 8px 2px $secondary-color;

  @media (min-width: 900px) {
    width: 300px;
    height: 330px;
    margin: 1rem;
  }

  &-details {
    display: flex;
    flex-direction: column;
    justify-content: center;

    @media (min-width: 900px) {
      flex-direction: row;
      justify-content: center;
    }

    &-poster {
      width: 60%;
      margin: auto;

      @media (min-width: 900px) {
        width: 40%;
        max-height: 150px; 
        object-fit: contain;
        margin: 0.5rem;
      }
    }

    &-information {
      width: 80%;
      margin: auto;
      text-align: left;

      @media (min-width: 900px) {
        width: 50%;
        padding: 0 10px;
      }

      a {
        display: inline-block;
        color: $secondary-color-alt;
        background: $main-color;
        padding: 0.8rem;
        border-radius: 10px;
        transition: background 0.25s, box-shadow 0.15s;

        &:hover {
          color: $third-color;
          background: $main-color;
          box-shadow: 3px 3px 5px 0px $secondary-color;
        }
      }
    }
  }
}
