@import 'src/_variables';

.excerpt {
  margin-bottom: 50px;
  text-align: left;

  @media (min-width: 900px) {
    width: 50%;
    margin: auto;
  }
}

.homepage {
  margin: 0 1rem;
  display: flex;
  flex-direction: column;

  &-events {
    order: 1;
    border-top: 5px solid $main-color-alt;

    .homepage-event {
      margin: 1rem 0;
      padding: 10px;
      box-shadow: 2px 2px 5px 5px $secondary-color-alt;
    }

    a {
      display: inline-block;
      color: $secondary-color-alt;
      background: $main-color;
      padding: 0.8rem;
      border-radius: 10px;
      transition: background 0.25s, box-shadow 0.15s;

      &:hover {
        color: $third-color;
        background: $main-color;
        box-shadow: 3px 3px 5px 0px $secondary-color;
      }
    }
  }

  .presentation {
    order: 0;
    border-top: 5px solid $main-color-alt;
    margin-bottom: 50px;
    line-height: 25px;
    text-align: left;

    a {
      display: inline-block;
      color: $secondary-color-alt;
      background: $main-color;
      padding: 0.2rem 0.8rem;
      border-radius: 10px;
      transition: background 0.25s, box-shadow 0.15s;

      &:hover {
        color: $third-color;
        background: $main-color;
        box-shadow: 3px 3px 5px 0px $secondary-color;
      }
    }
  }

  @media (min-width: 900px) {
    border-top: 5px solid $main-color-alt;
    flex-direction: row;

    &-events {
      order: 0;
      width: 40%;
      border: none;
      margin-right: 6rem;
    }

    .presentation {
      width: 60%;
      border: none;
    }
  }
}
